import React from "react";
import { Helmet } from "react-helmet";
import LayoutPage from "../Layout";
import axios from "axios";
import { toast } from "react-toastify";
import { useFormik } from "formik";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

function AddCategory() {
  const [disabledSubmit, setDisabledSubmit] = useState(false);
  const [initialValues, setInitialValues] = useState({
    sportLogo: "",
    sportName: "",
  });

  const [sportLogo, setformImage] = useState("");
  // const [initialValues, setInitialValues] = useState({ nationality_name: ''})
  const navigate = useNavigate();

  // const handleFileChange = event => {
  //     console.log(event.target.files[0]);

  // }
  const validate = (values) => {
    const errors = {};

    if (!values.sportLogo) {
      errors.sportLogo = " sport Logo is required";
    } else if (values.sportName.length < 3) {
      errors.sportName = " sportName min length is 3 characters";
    }
    return errors;
  };
  const formik = useFormik({
    initialValues: initialValues,
    validate,
    onSubmit: async (values) => {
      console.log("vall--", values);
      let formData = new FormData();
      formData.append("sportLogo", values.sportLogo);
      formData.append("sportName", values.sportName);
      setDisabledSubmit(true);
      try {
        let res = await axios.post(`admin/createSportsCategory`, formData);
        console.log(res);
        navigate("/category", { state: { message: res.data.data.message } });
      } catch (errors) {
        if (errors.response.data.error) {
          toast(errors.response.data.error.message, {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            type: "error",
          });
        } else {
          toast(errors.response.data.message, {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            type: "error",
          });
        }

        setDisabledSubmit(false);
      }
    },
  });

  return (
    <>
      <Helmet>
        <title>Add Category</title>
      </Helmet>
      <LayoutPage>
        <div className="row">
          <div className="col-12">
            <div className="card my-4">
              <div className="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
                <div
                  style={{
                    backgroundColor: "#46456e",
                  }}
                  className=" shadow-primary border-radius-lg pt-4 pb-3"
                >
                  <div className="d-flex ps-3 align-item-center">
                    <i
                      style={{ color: "#fff", cursor: "pointer" }}
                      className="fa fa-arrow-left fs-4"
                      onClick={() => {
                        navigate(-1);
                      }}
                    ></i>
                    <h6 className="text-white text-capitalize ps-4 ">
                      ADD NEW CATEGORY
                    </h6>
                  </div>
                </div>
              </div>
              <div className="card-body px-0 pb-2">
                <div className="p-4">
                  <form onSubmit={formik.handleSubmit}>
                    <div className="input-group input-group-outline mb-3">
                      <label className="fs-5">Sport Name</label>

                      <input
                        style={{ width: "100%" }}
                        type="text"
                        id="sportName"
                        name="sportName"
                        className="form-control fs-6"
                        autoComplete="off"
                        placeholder="Sport Name "
                        value={formik.values.sportName || ""}
                        onChange={formik.handleChange}
                      />
                    </div>
                    {formik.errors.sportName ? (
                      <div className="text-danger">
                        {formik.errors.sportName}
                      </div>
                    ) : null}
                    <div className="input-group input-group-outline mb-3">
                      <label className="fs-5 text-black">Sport Logo</label>
                      <input
                        // style={{ width: "100%" }}
                        type="file"
                        id="sportLogo"
                        name="sportLogo"
                        className="form-control fs-6 w-100"
                        // placeholder="sport Logo"
                        onChange={(event) => {
                          formik.setFieldValue(
                            "sportLogo",
                            event.target.files[0]
                          );
                        }}
                      />
                    </div>
                    {formik.errors.sportLogo ? (
                      <div className="text-danger">
                        {formik.errors.sportLogo}
                      </div>
                    ) : null}

                    <div className="text-center d-flex justify-content-center gap-4">
                      <button
                        type="submit"
                        className="btn btn-lg  btn-lg w-20 mt-4 mb-0"
                        disabled={disabledSubmit}
                        style={{
                          backgroundColor: "#46456e",
                          color: "#fff",
                        }}
                      >
                        {disabledSubmit ? (
                          <div>
                            <span
                              className="spinner-border spinner-border-sm"
                              role="status"
                              aria-hidden="true"
                            ></span>
                            <span className="sr-only"></span> Submitting
                          </div>
                        ) : (
                          "Submit"
                        )}
                      </button>
                      <button
                        type="button"
                        onClick={() => navigate("/category")}
                        className="btn btn-lg  btn-lg w-20 mt-4 mb-0"
                        disabled={disabledSubmit}
                        style={{
                          backgroundColor: "#46456e",
                          color: "#fff",
                        }}
                      >
                        Cancel
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </LayoutPage>
    </>
  );
}

export default AddCategory;
